import {
  Filter,
  Container,
  IResourceDictionary,
  Sprite,
  Texture, BLEND_MODES
} from "pixi.js"
import { MapBase } from "./map-base"
import { Config } from "../config"
import { Keyboard } from "../tools"
import { TextWithBackground } from "../objects/text-with-background"
import { GodrayFilter } from "@pixi/filter-godray"

/**
 *
 */
export class Title extends MapBase {

  enterText: Container
  bgm

  constructor(
    resources: IResourceDictionary,
    width: number,
    height: number
  ) {
    super(resources, width, "maps.maension");

    this.bgm = resources["bgm.title"].sound
    this.bgm.volume = 0.3
    this.bgm.play()

    this.withHud = false;
    this.withCrosshair = false;
    this.withPlayer = false;
    this.withMouse = false;

    const brand = new TextWithBackground(
      resources,
      "MÃ¤nsion",
      "54px",
      "#EFB775",
      undefined,
      "#111"
    )
    brand.x = width / 2 - brand.width / 2
    brand.y = height / 3 - brand.height / 2

    const isInteractive =
      window.location.search.endsWith("interactive") || !Config.lockTitleScreen

    let enterText = "Press Enter to play"
    if (!isInteractive) {
      enterText = "coming soon ..."
    }

    this.enterText = new Container();
    this.enterText.addChild(
      new TextWithBackground(resources, enterText, "24px"))

    this.enterText.x = width / 2 - this.enterText.width / 2
    this.enterText.y = height / 2 - this.enterText.height / 2

    let maension = new Sprite(
      this._textures!["Maension_WIP.png"]
    );

    maension.alpha = 0.6
    const scale = 2
    maension.scale.x = scale;
    maension.scale.y = scale;

    maension.y = height - maension.height
    maension.x = width / 2 - maension.width / 2

    this._foregroundContainer.addChild(maension, this.enterText, brand)

    {
      const god = new Sprite(
        Texture.WHITE
      );

      god.width = width
      god.height = height
      god.tint = Config.constants.black

      this.godFilter = new GodrayFilter()
      this.godFilter.angle = -35
      this.godFilter.gain = 0.5
      this.godFilter.lacunarity = 2.5
      god.filters = [this.godFilter]

      this._backgroundContainer.addChild(
        god
      )
    }

    {
      const rain = new Sprite(
        Texture.WHITE
      );

      rain.width = width
      rain.height = height
      rain.tint = Config.constants.black

      const rainFrag = resources["shaders.frag-rain"].data

      this.rainShader = new Filter(undefined, rainFrag);
      this.rainShader.uniforms.dimensions = [width, height];
      this.rainShader.uniforms.time = 1;
      this.rainShader.uniforms.u_mouse = [width, height];
      this.rainShader.uniforms.u_frame = 0;
      this.rainShader.blendMode = BLEND_MODES.ADD

      rain.filters = [this.rainShader]

      this._backgroundContainer.addChild(
        rain
      )
    }

    if (isInteractive) {
      this.wireEvents()
    }
  }

  private wireEvents() {
    const enter = new Keyboard("Enter", true);
    enter.pressEvent = () => this.done()

    this._foregroundContainer.interactive = true;
    this._foregroundContainer.on("touchstart", this.done.bind(this))
  }

  protected done() {
    this.bgm.stop();
    super.done();
  }

  rainShader
  godFilter
  count = 0;

  public tick(delta: number) {
    this.enterText.alpha += delta / 2000
    if (this.enterText.alpha >= 1) {
      this.enterText.alpha = 0.2
    }

    this.count += delta / 2000
    this.godFilter.time = this.count
    this.rainShader.uniforms.time = this.count;
  }
}